html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  background: #d8dcd2;
  max-width: 1200px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root {
  display: flex;
  width: 100%;
}

.container {
  margin: auto;
}

.help-row {
  display: flex;
}

.clock {
  display: flex
}
.item {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  margin: 10px 5px;
  background-color: #c6cac0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.item p {
  margin: 0;
  background-color: unset;
  color: #c6cac0;
  font-size: 100px;
  line-height: 1;
  font-family: monospace;
  text-align: center;
}

.lit {
  background-color: #292d23;
  -webkit-transition : 500ms ease-in;
  transition : 500ms ease-in;
}

.col:nth-child(1) .item:nth-child(-n+2), .col:nth-of-type(2n+3) .item:nth-child(1) {
  visibility: hidden;
}

.help-row .item {
  background-color: unset;
  color: #c6cac0;
  font-size: 100px;
  line-height: 0.7;
  font-family: monospace;
  text-align: center;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

